import { UniDriver } from '@wix/wix-ui-test-utils/unidriver';
import { baseUniDriverFactory } from '@wix/wix-ui-test-utils/base-driver';
import { PopoverUniDriver } from '@wix/design-system/dist/testkit/unidriver';

export function ErrorToastContentUniDriver(base: UniDriver, body: UniDriver) {
  return {
    ...baseUniDriverFactory(base),
    requestInfoSectionExists: async () =>
      base.$('[data-hook="request-info"]').exists(),
    getPopover: () =>
      PopoverUniDriver(base.$('[data-hook="info-popover"]'), body),
    getInfoDescriptionText: () =>
      base.$('[data-hook="info-description"]').text(),
    getRequestId: () => base.$('[data-hook="info-request-id"]').text(),
  };
}
